.login{
    padding: 50px;
}

.login-wrapper{
    background-image: url(/src/assets/img/login-bg.jpg);
    background-size: cover;
    background-position-y: -200px;
    height: 75vh;
    width: 100%;
    padding: 50px;
    position: relative;
}

.login-content{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 80%;
    width: 30%;
    position: absolute;
    margin: 25px;
}

.login-title{
    color: var(--clr-hl1);
}

.login-form{
    height: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.login-form-item{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 20%;
}

.login-form-text{
    height: 30px;
    border-radius: 5px;
    border: 1px solid var(--clr-dark);
}

.login-form-button{
    width: 100%;
    height: 40px;
    border-radius: 15px;
    background-color: var(--clr-hl1);
    font-size: var(--fs-body);
    cursor: pointer;
}

.login-create{
    color: var(--clr-hl1);
    display: flex;
    align-items: center;
    width: max-content;
    cursor: pointer;
}

.login-content-create{
    opacity: 0;
}

/* animations */

.login-anim-enter {
    opacity: 0;
    transform: translateX(20%);
}
.login-anim-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 300ms, transform 300ms;
}
.login-anim-exit {
    opacity: 1; 
}
.login-anim-exit-active {
    opacity: 0;
    transform: translateX(-20%);
    transition: opacity 300ms, transform 300ms;
}