@font-face {
  font-family: Thesignature;
  src: url('./assets/fonts/Thesignature.otf');
}

h1{
  font-weight: 50;
  font-size: var(--fs-h1);
}

h3{
  font-weight: 100;
  font-size: var(--fs-h3);
}

body {
  font-size: var(--fs-body);
  font-family: 'Radley', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input {
  font-family: 'Radley', serif;
}

table{
  border-collapse: collapse;
}

button{
  font-family: 'Radley', serif;
  font-size: var(--fs-body);
  cursor: pointer;
}

*{
  box-sizing: border-box;
  margin: 0;
  border: 0;
  padding: 0;
}


.link{
  text-decoration: none;
  color: inherit;
}

:root{
  --clr-dark: black;
  --clr-light: white;
  --clr-hl1: #E1A730;
  --clr-hl2: #C1FFF7;

  --fs-body: 16px;
  --fs-h1: 50px;
  --fs-h3: 25px;

  --header_height: 72px;
}
