.header{
    width: 100%;
    padding: 20px;
    height: var(--header_height);
    border-bottom: 3px solid var(--clr-hl1);
    z-index: 5;
    position: sticky;
    top: 0;
    background-color: var(--clr-light);
}

.header-logo{
    color: var(--clr-hl1);
}

.header-wrap{
    position: relative;
    background-color: var(--clr-light);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.header-options{
    text-decoration: none;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.header-option{
    margin: 0 15px 0 15px;
    cursor: pointer;
    font-size: 18px;
}

.header-option > a{
    text-decoration: none;
    color: inherit;
}

.header-icons{
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.header-icon{
    margin: 0 10px 0 10px;
    cursor: pointer;
}

.header-dd-menu-wrapper{
    display: none;
    position: absolute;
    z-index: 5;

}

.header-dd-menu{
    list-style: none;
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: var(--clr-light);
    border: 3px solid var(--clr-hl1);
    
}

.header-dd-menu-item{
    height: 40px;
    background-color: var(--clr-light);
    display: flex;
    align-items: center;
    padding: 20px 20px 20px 0px;
    transition: text-decoration-color 0.3s;
    text-decoration: underline;
    text-decoration-color: rgba(0, 0, 0, 0);
}

.header-dd-menu-item:hover{
    text-decoration-color: black;

}

.header-dd-menu-item > a{
    color: inherit;
    text-decoration: none;
}

.header-dd:hover .header-dd-menu-wrapper{
    display: block;
    padding-top: 50px;
}

/* error page */

.error-page {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

/* footer */

.footer{
    margin: 10vh 50px 0 50px;
    height: 200px;
    border-top: 3px solid var(--clr-hl1);
    display: flex;
    position: relative;
    justify-content: center;
}

.footer-contact{
    width: 30%;
}

.footer-socials{
    width: 30%;
}

.footer-logo{
    width: 30%;
}

.footer-wrapper{
    margin-top: 50px;
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.footer-contact-item{
    display: flex;
    margin-top: 10px;
}

.footer-contact-icon{
    margin-right: 10px;
}

.footer-socials-icons{
    margin-top: 10px;
    display: flex;
    flex-direction: row;
}

.footer-logo{
    display: flex;
    justify-content: end;
}

.footer-logo-img{
    height: 100px;
}
