
.flower-detail{
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: var(--clr-light);
    z-index: 10;
    display: flex;
    overflow: hidden;
    justify-content: center;
    align-items: center;
}

.flower-page{
    position: relative;
    width: 100%;
    height: 100%;
    background-color: var(--clr-light);
    display: flex;
}

.flower-page-img{
    height: 100%;
}