.cart{
    margin: 50px;
}

.cart-header{
    display: flex;
    justify-content: center;
    border-bottom: 1px solid var(--clr-dark);

}

.cart-title{
    color: var(--clr-hl1);
}

.cart-content{
    width: 100%;
}

.cart-content-header-wrapper{
    width: 100%;
    height: 15vh;
    border-bottom: 1px solid var(--clr-dark);
}

.cart-content-header{
    width: 25%;
    font-size: var(--fs-body);
}

.cart-item{
    width: 100%;
}

.cart-item-img{
    padding: 30px 0 30px 0;
    height: 40vh;
}

.cart-item-title{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 30px;
}

.cart-item-quantity{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

    margin: 30px;
}

.cart-item-quantity-btn{
    width: 50px;
    height: 50px;
    background-color: transparent;
}

.cart-item-total{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin: 30px;
}

.cart-item-btn{
    width: 120px;
    height: 40px;
    background-color: transparent;
    display: flex;
    color: var(--clr-hl1);
    align-items: center;
}

.cart-footer{
    border-top: 1px solid var(--clr-dark);
    display: flex;
    width: 100%;
    height: 20vh;
    flex-direction: column;
    justify-content: space-around;
    align-items: end;
}

.cart-order-btn{
    width: 120px;
    height: 50px;
    background-color: var(--clr-hl1);
    border-radius: 25px;
}

.cart-empty{
    border-bottom: 1px solid var(--clr-dark);
    height: 40vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}